// npm i sortablejs 
// 
import Sortable from "sortablejs";
import { ref } from "vue";
import { reviseRank } from "@/api/article";//
import { ElMessage } from "element-plus";
// index,vue el-table ref ="tableRef" 
export const tableRef = ref(null);
// 
export const initSortable = (articleList, cb) => {
  console.log(tableRef.value);
  let el = tableRef.value.$el.querySelector(".el-table__body tbody"); //   tbody
  // sortable
  Sortable.create(el, {
    // 
    ghostClass: "sortable-ghost",
    //  
    async onEnd(event) {
      //   
      //  
      const { newIndex, oldIndex } = event;
      console.log(":", newIndex);
      console.log(":", oldIndex);

      // reviseRank man  woman 
      let man = articleList.value[newIndex];
      let woman = articleList.value[oldIndex];
      const { data } = await reviseRank({
        man,
        woman,
      });
      ElMessage.success(data.txt);
      //  el-table  
      articleList.value = [];
      //  cbindex.vuegetArticleListFn
      cb();
    },
  });
};
