// 1.

import { ref, computed } from "vue";
//    prop
const dynamicRaw = [
  { label: "", prop: "rank" },
  { label: "", prop: "title" },
  { label: "", prop: "author" },
  { label: "", prop: "date" },
  { label: "", prop: "description" },
];
// 
// 
export const dynamicData = ref(dynamicRaw); // 

//  ()
export const selectDynamic = ref([]);

const initSelectDynamic = () => {
 
  // lable===>['', '']
  selectDynamic.value = dynamicData.value.map((item) => item.label);
};
initSelectDynamic();

//   table tableColumnsselectDynamic
//   selectDynamic
// selectDynamic.value.includes(item.label)false tableColumns
export const tableColumns = computed(() => {
  // filter()
  return dynamicData.value.filter((item) =>
    // includes() truefalse
    selectDynamic.value.includes(item.label)
  );
});
